import { Component, OnInit } from '@angular/core';

import { QuizService } from '../../../@core/data/quiz.service';
import { HelperService } from '../../../@core/data/helper.service';
import { Option, Question, Quiz, QuizConfig } from '../../../models';
import { ShufflePipe } from 'ngx-pipes';

@Component({
    selector: 'ngx-quiz-app',
    styleUrls: ['./quiz-app.component.scss'],
    templateUrl: './quiz-app.component.html',
    providers: [ShufflePipe]
})
export class QuizAppComponent implements OnInit {
    AnswerStatus = '';
    quizes: any[];
    quiz: Quiz = new Quiz(null);
    mode = 'quiz';
    quizName: string;
    config: QuizConfig = {
        'allowBack': true,
        'allowReview': true,
        autoMove: true,  // if true, it will move to next question automatically when answered.
        'duration': 9000,  // indicates the time in which quiz needs to be completed. 0 means unlimited.
        'pageSize': 1,
        'requiredAll': false,  // indicates if you must answer all the questions before submitting.
        'richText': false,
        'shuffleQuestions': true,
        'shuffleOptions': true,
        'showClock': true,
        'showPager': true,
        'theme': 'none'
    };

    pager = {
        index: 0,
        size: 1,
        count: 1
    };

    constructor(private quizService: QuizService,
        private shufflePipe: ShufflePipe
    ) { }

    ngOnInit() {
        this.quizes = this.quizService.getAll();
        this.quizName = this.quizes[0].id;
        this.loadQuiz(this.quizName);
    }

    loadQuiz(quizName: string) {
        this.quizService.get(quizName).subscribe(res => {
            this.quiz = new Quiz(res);
            if (this.quiz.config.shuffleQuestions) {
                this.quiz.questions = this.shufflePipe.transform(this.quiz.questions);
            }
            if (this.quiz.config.shuffleQuestions) {
                this.quiz.questions.forEach(quest => {
                    quest.options = this.shufflePipe.transform(quest.options);
                });
            }
            this.pager.count = this.quiz.questions.length;
        });
        this.mode = 'quiz';
    }

    get filteredQuestions() {
        return (this.quiz.questions) ?
            this.quiz.questions.slice(this.pager.index, this.pager.index + this.pager.size) : [];
    }

    onSelect(question: Question, option: Option) {
        if (question.questionTypeId === 1) {
            question.options.forEach((x) => { if (x.id !== option.id) x.selected = false; });
        }
        this.AnswerStatus = this.isCorrect(question);
        let that = this;
        if (this.AnswerStatus === 'correct' && this.config.autoMove) {
            setTimeout(function () {
                that.goTo(that.pager.index + 1);
                that.AnswerStatus = '';
            }, 1000);
        }
    }

    goTo(index: number) {
        if (index >= 0 && index < this.pager.count) {
            this.pager.index = index;
            this.mode = 'quiz';
        }
    }

    isAnswered(question: Question) {
        return question.options.find(x => x.selected) ? 'Answered' : 'Not Answered';
    };

    isCorrect(question: Question) {
        return question.options.every(x => x.selected === x.isAnswer) ? 'correct' : 'wrong';
    };

    onSubmit() {
        let answers = [];
        this.quiz.questions.forEach(x => answers.push({ 'quizId': this.quiz.id, 'questionId': x.id, 'answered': x.answered }));

        // Post your data to the server here. answers contains the questionId and the users' answer.
        console.log(this.quiz.questions);
        this.mode = 'result';
    }
}



